.shop{
    padding-top: 100px;
    background-color: var(--clr-bg);
}

.shop__center{
    display: flex;
    flex-wrap: wrap;
}

.shop__filter{
    display: flex;
    justify-content: flex-end;
    position: relative;
    z-index: 9;
    margin-bottom: 30px;
    padding-bottom: 3px;
    /* width: 64%; */
}
.shop__filter > span.filter__icon{
    cursor: pointer;
    background-color: white;
    padding: 6px 6px;
    background-color: var(--clr-sec);
    border-radius: 3px;
    display: flex;
    
}
.shop__filter > span svg{
    font-size: 16px;
    fill: white;
}
.shop__filter__indicator{
    width: 10px;
    height: 10px;
    align-items: center;
    justify-content: center;
    background-color: rgb(255, 166, 0);
    border-radius: 50%;
    position: absolute;
    right: -4.5px;
    top: -4.5px;
    font-size: 14px;
    color: white;
    display: none;
}
.shop__filter__indicator.show{
    display: flex;
}
.filter__options{
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
    background-color: white;
    padding: 10px 10px;
    border-radius: 4px;
    box-shadow: 0 0 2px 1px rgba(230, 229, 229, 0.644);
}
.filter__options span{
    display: block;
    text-transform: capitalize;
    letter-spacing: 1px;
    font-size: 14px;
    padding: 8px 0;
    cursor: pointer;
}
.filter__options span.active{
    color: orange;
}
.filter__options span:hover{
    opacity: .8;
}
.filter__options.show{
    display: block;
}

.shop-empty{
    height: 100vh;
    background-color: var(--clr-bg);
}
.shop__empty__center{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.shop__empty__center h1{
    font-size: 24px;
    font-weight: 400; 
}



