.fetch__error{
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    
    z-index: 6;

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
}
.fetch__error h1{
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 16px;
}

